.Admin {
  .HeaderAdmin,
  .SidebarAdmin {
    .page-body-wrapper {
      min-height: calc(100vh - 64px);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      padding-left: 0;
      padding-right: 0;
    }
  }
}